import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axiosInstance from "@/api/axios/axiosInstance";
import StudyAnalysisStepper from "./StudyAnalysisStepper";

const Studies_Calculator = () => {
  const [studies, setStudies] = useState([]);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);

  const [selectedStudy, setSelectedStudy] = useState(null);
  const [sidebarLoading, setSidebarLoading] = useState(false);

  const [activeTab, setActiveTab] = useState("list");
  const [syncLoading, setSyncLoading] = useState(false);
  const [showFilters, setShowFilters] = useState(false);
  const [selectedStudyId, setSelectedStudyId] = useState(null);

  const navigate = useNavigate();
  // Dynamic filter states
  const [filterOptions, setFilterOptions] = useState({
    phases: [],
    conditions: [],
    locations: [],
    enrollmentStats: { min: 0, max: 10000 },
  });
  const [conditionSuggestions, setConditionSuggestions] = useState([]);
  const [locationSuggestions, setLocationSuggestions] = useState([]);
  const [showConditionSuggestions, setShowConditionSuggestions] =
    useState(false);
  const [showLocationSuggestions, setShowLocationSuggestions] = useState(false);

  const [filters, setFilters] = useState({
    phase: [],
    condition: "",
    location: "",
    enrollmentMin: "",
    enrollmentMax: "",
  });

  const [calcNctId, setCalcNctId] = useState("");
  const [calcStudy, setCalcStudy] = useState(null);
  const [calcLoading, setCalcLoading] = useState(false);
  const [calcError, setCalcError] = useState("");

  const limit = 10;

  const openStudy = async (studyId) => {
    try {
      setSidebarLoading(true);
      setSelectedStudy(null);
      setSelectedStudyId(studyId);

      const res = await axiosInstance.get(`/api/studies/db/${studyId}`);

      setSelectedStudy(res.data.study.raw_json);
    } catch (err) {
      console.error(err);
    } finally {
      setSidebarLoading(false);
    }
  };

  // Fetch dynamic filter options
  const fetchFilterOptions = async () => {
    try {
      const res = await axiosInstance.get(
        `${
          import.meta.env.VITE_APP_BASE_URL
        }/api/studies/clinical-trails-gov/filter-options`
      );

      setFilterOptions({
        phases: res.data.phases || [],
        conditions: res.data.conditions || [],
        locations: res.data.locations || [],
        enrollmentStats: res.data.enrollmentStats || { min: 0, max: 10000 },
      });

      // Set initial enrollment range
      if (res.data.enrollmentStats) {
        setFilters((prev) => ({
          ...prev,
          enrollmentMin: res.data.enrollmentStats.min,
          enrollmentMax: res.data.enrollmentStats.max,
        }));
      }
    } catch (err) {
      console.error("Error fetching filter options:", err);
    }
  };

  // Fetch available locations
  const fetchAvailableLocations = async () => {
    try {
      const res = await axiosInstance.get(
        `${
          import.meta.env.VITE_APP_BASE_URL
        }/api/studies/clinical-trails-gov/available-locations`
      );
      setFilterOptions((prev) => ({
        ...prev,
        locations: res.data.locations || [],
      }));
    } catch (err) {
      console.error("Error fetching locations:", err);
    }
  };

  // Search conditions (autocomplete)
  const searchConditionSuggestions = async (query) => {
    if (!query || query.length < 2) {
      setConditionSuggestions([]);
      setShowConditionSuggestions(false);
      return;
    }

    try {
      const res = await axiosInstance.get(
        `${
          import.meta.env.VITE_APP_BASE_URL
        }/api/studies/clinical-trails-gov/search-conditions`,
        { params: { query } }
      );

      setConditionSuggestions(res.data.results || []);
      setShowConditionSuggestions(true);
    } catch (err) {
      console.error("Error searching conditions:", err);
    }
  };

  // Search locations (autocomplete)
  const searchLocationSuggestions = async (query) => {
    if (!query || query.length < 2) {
      setLocationSuggestions([]);
      setShowLocationSuggestions(false);
      return;
    }

    try {
      const res = await axiosInstance.get(
        `${
          import.meta.env.VITE_APP_BASE_URL
        }/api/studies/clinical-trails-gov/search-locations`,
        { params: { query } }
      );

      setLocationSuggestions(res.data.results || []);
      setShowLocationSuggestions(true);
    } catch (err) {
      console.error("Error searching locations:", err);
    }
  };

  // Fetch studies from database
  const fetchStudies = async (pageNum = 1) => {
    try {
      setLoading(true);
      const params = {
        page: pageNum,
        limit,
        search,
        ...filters,
      };

      const res = await axiosInstance.post(
        "/api/studies/clinical-trails-gov/studies",
        {
          page: pageNum,
          limit,
          expertQuery: {
            text: search,
            logic: "AND",
            phase: filters.phase,
            locations: filters.location ? { city: filters.location } : null,
            enrollment: {
              min: filters.enrollmentMin || null,
              max: filters.enrollmentMax || null,
            },
          },
        }
      );
      console.log("++++++++++++++++++++++++++++");
      console.log("API Response:", res.data);

      setStudies(res.data.studies || []);
      setTotal(res.data.total || 0);
      setTotalPages(res.data.totalPages || 1);
      setPage(pageNum);
    } catch (err) {
      console.error("Error fetching studies:", err);
      setStudies([]);
    } finally {
      setLoading(false);
    }
  };

  // Sync studies from ClinicalTrials.gov to database
  const syncStudies = async () => {
    try {
      setSyncLoading(true);
      const res = await axiosInstance.get(
        `${
          import.meta.env.VITE_APP_BASE_URL
        }/api/studies/clinical-trails-gov/all-studies`
      );

      alert(
        ` Synced successfully!\n${res.data.message}\nTotal studies synced: ${res.data.total}`
      );

      // Refresh filter options and studies
      await fetchFilterOptions();
      await fetchAvailableLocations();
      fetchStudies(1);
    } catch (err) {
      console.error("Error syncing studies:", err);
      alert(" Failed to sync studies. Check console for details.");
    } finally {
      setSyncLoading(false);
    }
  };

  // Handle filter changes
  const handleFilterChange = (filterName, value, isMultiple = false) => {
    if (isMultiple) {
      setFilters((prev) => ({
        ...prev,
        [filterName]: prev[filterName].includes(value)
          ? prev[filterName].filter((item) => item !== value)
          : [...prev[filterName], value],
      }));
    } else {
      setFilters((prev) => ({
        ...prev,
        [filterName]: value,
      }));
    }
  };

  const handleConditionChange = (condition) => {
    setFilters((prev) => ({
      ...prev,
      condition,
    }));
    setShowConditionSuggestions(false);
  };

  const handleLocationChange = (location) => {
    setFilters((prev) => ({
      ...prev,
      location,
    }));
    setShowLocationSuggestions(false);
  };

  const clearFilters = () => {
    setFilters({
      phase: [],
      condition: "",
      location: "",
      enrollmentMin: filterOptions.enrollmentStats.min,
      enrollmentMax: filterOptions.enrollmentStats.max,
    });
    setPage(1);
  };

  useEffect(() => {
    fetchFilterOptions();
    fetchAvailableLocations();
  }, []);

  useEffect(() => {
    fetchStudies(1);
  }, [search, filters]);

  const getStudyDurationMonths = (startDate, endDate) => {
    if (!startDate || !endDate) return "-";

    const start = new Date(startDate);
    const end = new Date(endDate);

    if (isNaN(start) || isNaN(end)) return "-";

    const yearsDiff = end.getFullYear() - start.getFullYear();
    const monthsDiff = end.getMonth() - start.getMonth();

    return yearsDiff * 12 + monthsDiff;
  };

  return (
    <div
      className="min-h-screen  py-10 bg-gradient-to-r
        from-[#0b0f2e]
        via-[#0e1340]
        to-[#0b0f2e]
  text-slate-100"
    >
      <div className="max-w-auto mx-auto px-6">
        <div className="mb-6 flex gap-3 border-b border-white/10 pb-3">
          {/* ALL STUDIES */}
          <button
            onClick={() => setActiveTab("list")}
            className={`
      relative px-6 py-2 text-sm font-semibold transition-all
      ${
        activeTab === "list"
          ? `
            text-white
            bg-gradient-to-r from-cyan-500/30 to-blue-600/30
            
            shadow-[0_0_30px_rgba(34,211,238,0.6)]
            flag-tab
          `
          : `
            text-white/50
            hover:text-white
            hover:bg-white/5
            rounded-md
          `
      }
    `}
          >
            All Studies
          </button>

          {/* CALCULATE */}
          <button
            onClick={() => setActiveTab("calculate")}
            className={`
      relative px-6 py-2 text-sm font-semibold transition-all
      ${
        activeTab === "calculate"
          ? `
            text-white
            bg-gradient-to-r from-blue-500/30 to-indigo-600/30
            
            shadow-[0_0_30px_rgba(59,130,246,0.6)]
            flag-tab
          `
          : `
            text-white/50
            hover:text-white
            hover:bg-white/5
            rounded-md
          `
      }
    `}
          >
            Calculate Study
          </button>
        </div>

        {activeTab === "list" && (
          <>
            {/* Header */}
            <div className="mb-6">
              <h1 className="text-xl font-bold">Clinical Trials Analysis</h1>
              <p className="text-white/70 mt-1">Search and analyze studies</p>
            </div>

            {/* Search & Sync Button */}
            <div className="mb-4 flex gap-3 items-center">
              <input
                type="text"
                placeholder="Search: 'cancer in Houston' or NCT ID, Title, Condition, or Location"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                className="flex-1 px-4 py-2 rounded-xl
  bg-white/5 backdrop-blur-md
  border border-white/10
  text-white placeholder:text-slate-400
  focus:outline-none focus:ring-2 focus:ring-cyan-400/60
  shadow-[inset_0_0_12px_rgba(255,255,255,0.05)]"
              />

              {/* <button
                onClick={() => setShowFilters(!showFilters)}
                className="px-4 py-2 bg-gradient-to-br from-cyan-500 to-blue-600
text-white rounded-xl
shadow-[0_8px_30px_rgba(56,189,248,0.45)]
hover:shadow-[0_0_40px_rgba(56,189,248,0.9)]
transition-all duration-300"
              >
                 Filters
              </button> */}
              <button
                onClick={() => setShowFilters(!showFilters)}
                className="
    px-4 py-2
    rounded-lg
    text-sm font-medium
    text-cyan-300
    bg-cyan-400/10
    border border-cyan-400/30
    shadow-[0_3px_5px_rgba(34,211,238,0.35)]
    hover:bg-cyan-400/20
    hover:shadow-[0_10px_10px_rgba(34,211,238,0.55)]
    transition-all
    flex items-center gap-2
  "
              >
                 Filters
              </button>

              <button
                onClick={syncStudies}
                disabled={syncLoading}
                className="
    px-5 py-2
    rounded-lg
    text-sm font-semibold
    text-white
    bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
    border border-white/15
    shadow-[0_2px_5px_rgba(90,110,255,0.45)]
    hover:shadow-[0_5px_10px_rgba(90,110,255,0.65)]
    hover:bg-[linear-gradient(180deg,rgba(90,110,255,0.6),rgba(36,46,151,0.45))]
    disabled:opacity-40
    disabled:cursor-not-allowed
    transition-all
    flex items-center gap-2
  "
              >
                {syncLoading ? "Syncing..." : " Sync"}
              </button>
            </div>

            {/* Filters Section - Dynamic */}
            {showFilters && (
              <div
                className="mb-6 bg-white/5 backdrop-blur-xl
border border-white/10
p-6 rounded-2xl
shadow-[0_0_40px_rgba(255,255,255,0.05)] space-y-4"
              >
                <div className="flex justify-between items-center">
                  <h3 className="text-lg font-semibold">Advanced Filters</h3>
                  <button
                    onClick={clearFilters}
                    className="px-3 py-1 text-sm bg-red-100 text-red-700 rounded hover:bg-red-200"
                  >
                    Clear All
                  </button>
                </div>

                {/* Phase Filter - Dynamic */}
                {filterOptions.phases.length > 0 && (
                  <div>
                    <label className="block text-sm font-medium mb-2">
                      Study Phase ({filterOptions.phases.length})
                    </label>
                    <div className="flex flex-wrap gap-2">
                      {filterOptions.phases.map((phase) => (
                        <label key={phase} className="flex items-center">
                          <input
                            type="checkbox"
                            checked={filters.phase.includes(phase)}
                            onChange={() =>
                              handleFilterChange("phase", phase, true)
                            }
                            className="mr-2"
                          />
                          <span className="text-sm">{phase}</span>
                        </label>
                      ))}
                    </div>
                  </div>
                )}

                {/* Condition Filter - Dynamic with Autocomplete */}
                {filterOptions.conditions.length > 0 && (
                  <div>
                    <label className="block text-sm font-medium mb-2">
                      Condition ({filterOptions.conditions.length})
                    </label>
                    <div className="relative">
                      <input
                        type="text"
                        placeholder="Search condition (e.g., diabetes, cancer)"
                        value={filters.condition}
                        onChange={(e) => {
                          handleFilterChange("condition", e.target.value);
                          searchConditionSuggestions(e.target.value);
                        }}
                        onFocus={() =>
                          filters.condition && setShowConditionSuggestions(true)
                        }
                        className="w-full px-4 py-2 border rounded-lg"
                      />
                      {showConditionSuggestions &&
                        conditionSuggestions.length > 0 && (
                          <div className="absolute top-full left-0 right-0 mt-1 bg-white border rounded-lg shadow-lg z-10 max-h-48 overflow-y-auto">
                            {conditionSuggestions.map((condition) => (
                              <div
                                key={condition}
                                onClick={() => handleConditionChange(condition)}
                                className="px-4 py-2 hover:bg-blue-50 cursor-pointer border-b last:border-b-0"
                              >
                                {condition}
                              </div>
                            ))}
                          </div>
                        )}
                    </div>
                  </div>
                )}

                {/* Enrollment Range Filter - Dynamic */}
                {filterOptions.enrollmentStats && (
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium mb-2">
                        Min Enrollment (0 - {filterOptions.enrollmentStats.max})
                      </label>
                      <input
                        type="number"
                        placeholder="0"
                        value={filters.enrollmentMin}
                        onChange={(e) =>
                          handleFilterChange("enrollmentMin", e.target.value)
                        }
                        min={filterOptions.enrollmentStats.min}
                        max={filterOptions.enrollmentStats.max}
                        className="w-full px-4 py-2 border rounded-lg"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2">
                        Max Enrollment (0 - {filterOptions.enrollmentStats.max})
                      </label>
                      <input
                        type="number"
                        placeholder={filterOptions.enrollmentStats.max}
                        value={filters.enrollmentMax}
                        onChange={(e) =>
                          handleFilterChange("enrollmentMax", e.target.value)
                        }
                        min={filterOptions.enrollmentStats.min}
                        max={filterOptions.enrollmentStats.max}
                        className="w-full px-4 py-2 border rounded-lg"
                      />
                    </div>
                  </div>
                )}
              </div>
            )}

            {/* Stats */}
            <div className="mb-4 text-sm text-gray-600">
              Total Studies: <strong>{total}</strong>
              {search && (
                <span className="ml-2">
                  | Search: <strong>"{search}"</strong>
                </span>
              )}
            </div>

            {/* Table */}
            <div
              className="bg-white/5 backdrop-blur-xl
border border-white/10
rounded-2xl overflow-x-auto
shadow-[0_0_40px_rgba(255,255,255,0.05)]"
            >
              <table className="min-w-full text-sm">
                <thead className="bg-white/10 backdrop-blur-md text-cyan-300">
                  <tr>
                    <th className="p-3 text-left">NCT ID</th>
                    <th className="p-3 text-left">Title</th>
                    <th className="p-3 text-left">Phase</th>
                    <th className="p-3 text-left">Condition</th>
                    <th className="p-3 text-left">Location</th>
                    <th className="p-3 text-left">Enrollment</th>
                    <th className="p-3 text-left">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {loading ? (
                    <tr>
                      <td colSpan="6" className="p-4 text-center">
                        Loading...
                      </td>
                    </tr>
                  ) : studies.length === 0 ? (
                    <tr>
                      <td colSpan="6" className="p-4 text-center">
                        No studies found. Click "Sync" to fetch studies.
                      </td>
                    </tr>
                  ) : (
                    studies.map((s) => (
                      <tr
                        key={s.nct_id}
                        className="
  border-t border-white/5
  cursor-pointer
  hover:bg-white/5
  hover:shadow-[inset_0_0_20px_rgba(56,189,248,0.15)]
  transition
"
                      >
                        <td className="p-3 font-medium">{s.nct_id}</td>
                        <td
                          className="p-3 text-blue-600 hover:underline"
                          onClick={() => openStudy(s.id)}
                        >
                          {s.title}
                        </td>
                        <td className="p-3">{s.phase || "-"}</td>
                        <td className="p-3">{s.condition_name || "-"}</td>
                        <td className="p-3 text-sm">
                          {s.raw_json?.protocolSection?.contactsLocationsModule
                            ?.locations?.length ? (
                            <span
                              className="
        inline-flex items-center gap-1
        px-3 py-1
        rounded-full
        text-xs font-medium
        text-cyan-300
        bg-cyan-400/10
        border border-cyan-400/25
        shadow-[0_0_10px_rgba(34,211,238,0.35)]
        backdrop-blur-sm
      "
                            >
                              
                              {
                                s.raw_json.protocolSection
                                  .contactsLocationsModule.locations[0].city
                              }
                              ,{" "}
                              {
                                s.raw_json.protocolSection
                                  .contactsLocationsModule.locations[0].country
                              }
                            </span>
                          ) : (
                            <span className="text-white/40"></span>
                          )}
                        </td>

                        <td className="p-3">{s.enrollment || "-"}</td>
                        <td className="p-3">
                          <button
                            onClick={() => navigate(`/studies/db/${s.id}`)}
                            className="neo-btn"
                          >
                            Details
                          </button>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>

            {/* Pagination */}

            <div className="flex justify-between items-center mt-6">
              {/* Previous */}
              <button
                disabled={page === 1 || loading}
                onClick={() => fetchStudies(page - 1)}
                className="
      px-4 py-2
      rounded-lg
      text-sm font-medium
      text-white
      bg-white/5
      border border-white/10
      shadow-[0_6px_20px_rgba(0,0,0,0.6)]
      hover:bg-white/10
      hover:shadow-[0_10px_30px_rgba(90,110,255,0.35)]
      disabled:opacity-40
      disabled:cursor-not-allowed
      transition-all
    "
              >
                 Previous
              </button>

              {/* Page Indicator */}
              <span
                className="
      text-sm font-medium
      text-white/70
      px-4 py-1.5
      rounded-full
      bg-white/5
      border border-white/10
    "
              >
                Page <span className="text-white">{page}</span> of{" "}
                <span className="text-white">{totalPages}</span>
              </span>

              {/* Next */}
              <button
                disabled={page === totalPages || loading}
                onClick={() => fetchStudies(page + 1)}
                className="
      px-4 py-2
      rounded-lg
      text-sm font-medium
      text-white
      bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
      border border-white/15
      shadow-[0_8px_26px_rgba(90,110,255,0.45)]
      hover:shadow-[0_12px_38px_rgba(90,110,255,0.65)]
      hover:bg-[linear-gradient(180deg,rgba(90,110,255,0.6),rgba(36,46,151,0.45))]
      disabled:opacity-40
      disabled:cursor-not-allowed
      transition-all
    "
              >
                Next 
              </button>
            </div>
          </>
        )}

        {activeTab === "calculate" && (
          <div className="space-y-6">
            <div
              className="
    flex gap-4 items-center
    p-5 rounded-2xl
    bg-[linear-gradient(180deg,#0b0f2e_0%,#090c26_100%)]
    border border-white/10
    shadow-[0_30px_80px_rgba(0,0,0,0.7)]
  "
            >
              <input
                type="text"
                placeholder="Enter NCT ID (e.g. NCT04838808)"
                value={calcNctId}
                onChange={(e) => setCalcNctId(e.target.value.toUpperCase())}
                className="
    px-4 py-2 w-80 rounded-lg
    bg-black/30
    border border-cyan-400/20
    text-white placeholder-white/40
    focus:outline-none focus:ring-2 focus:ring-cyan-400/50
  "
              />

              <button
                onClick={async () => {
                  if (!calcNctId) return;

                  try {
                    setCalcLoading(true);
                    setCalcError("");
                    setCalcStudy(null);

                    const res = await axiosInstance.get(
                      `${
                        import.meta.env.VITE_APP_BASE_URL
                      }/api/studies/clinical-trails-gov/live/${calcNctId}`
                    );

                    setCalcStudy(res.data.study);
                  } catch (err) {
                    setCalcError("Study not found on ClinicalTrials.gov");
                  } finally {
                    setCalcLoading(false);
                  }
                }}
                className="
    px-5 py-2 rounded-lg font-medium
    text-white
    bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
    shadow-[0_0_5px_rgba(34,211,238,0.7)]
    hover:shadow-[0_0_8px_rgba(34,211,238,1)]
    transition-all
    disabled:opacity-50
    disabled:cursor-not-allowed
  "
              >
                Load Study
              </button>
            </div>

            {calcLoading && <p className="text-gray-600">Loading study</p>}
            {calcError && <p className="text-red-600">{calcError}</p>}
            {calcStudy && (
              <div
                className="
                p-6 rounded-2xl space-y-6
                bg-[linear-gradient(180deg,#0b0f2e_0%,#090c26_100%)]
                border border-white/10
                shadow-[0_30px_80px_rgba(0,0,0,0.7)]
              "
              >
                <div>
                  <h2 className="text-lg font-semibold text-white">
                    {calcStudy.protocolSection.identificationModule.nctId}
                  </h2>
                  <p className="text-white/60 mt-1">
                    {calcStudy.protocolSection.identificationModule.briefTitle}
                  </p>
                </div>

                <div
                  className="
    grid grid-cols-2 gap-4 p-4 rounded-xl text-sm
    bg-black/30
    border border-white/10
  "
                >
                  <div>
                    <strong>Phase:</strong>{" "}
                    {calcStudy.protocolSection.designModule?.phases?.join(", ")}
                  </div>
                  <div>
                    <strong>Status:</strong>{" "}
                    {calcStudy.protocolSection.statusModule?.overallStatus}
                  </div>
                  <div>
                    <strong>Enrollment:</strong>{" "}
                    {calcStudy.protocolSection.designModule?.enrollmentInfo
                      ?.count || "-"}
                  </div>
                  <div>
                    <strong>Conditions:</strong>{" "}
                    {calcStudy.protocolSection.conditionsModule?.conditions?.join(
                      ", "
                    )}
                  </div>
                </div>
              </div>
            )}
            {calcStudy && <StudyAnalysisStepper study={calcStudy} />}
          </div>
        )}
      </div>
      {(sidebarLoading || selectedStudy) && (
        <div
          className="
      fixed top-0 right-0 h-full w-1/2
      bg-gradient-to-r
        from-[#0b0f2e]
        via-[#0e1340]
        to-[#0b0f2e]
      backdrop-blur-xl
      border-l border-white/10
      shadow-[0_0_80px_rgba(36,46,151,0.35)]
      z-50 flex flex-col
    "
        >
          {/* HEADER */}
          <div
            className="
        px-6 py-4
        flex justify-between items-center
        border-b border-white/10
        bg-[linear-gradient(180deg,rgba(63,78,255,0.18),rgba(9,12,38,0.9))]
      "
          >
            <div>
              <h2 className="text-lg font-semibold text-white">
                {selectedStudy?.protocolSection?.identificationModule?.nctId ||
                  "Loading..."}
              </h2>
              <p className="text-sm text-white/60">
                ClinicalTrials.gov  Live Protocol
              </p>
            </div>

            <div className="flex items-center gap-3">
              <button
                onClick={() => navigate(`/studies/db/${selectedStudyId}`)}
                className="neo-btn"
              >
                View Full Details
              </button>

              <button
                onClick={() => setSelectedStudy(null)}
                className="
            w-8 h-8 flex items-center justify-center
            rounded-full
            text-white/60
            hover:text-white
            hover:bg-white/10
            transition
          "
                aria-label="Close sidebar"
              >
                
              </button>
            </div>
          </div>

          {/* CONTENT */}
          <div className="p-6 overflow-y-auto text-sm space-y-8">
            {sidebarLoading ? (
              <p className="text-white/60">Fetching live data</p>
            ) : selectedStudy ? (
              (() => {
                const status = selectedStudy?.protocolSection?.statusModule;
                const startDate = status?.startDateStruct?.date || "-";
                const endDate =
                  status?.completionDateStruct?.date ||
                  status?.primaryCompletionDateStruct?.date ||
                  "-";
                const duration =
                  startDate !== "-" && endDate !== "-"
                    ? `${getStudyDurationMonths(startDate, endDate)} months`
                    : "-";

                return (
                  <>
                    {/* TITLE */}
                    <div>
                      <h3 className="text-base font-semibold text-white">
                        {
                          selectedStudy.protocolSection.identificationModule
                            .briefTitle
                        }
                      </h3>
                      <p className="text-white/60 mt-1">
                        {
                          selectedStudy.protocolSection.descriptionModule
                            ?.briefSummary
                        }
                      </p>
                    </div>

                    {/* META GRID */}
                    <div className="grid grid-cols-2 gap-4">
                      {[
                        [
                          "Phase",
                          selectedStudy.protocolSection.designModule?.phases?.join(
                            ", "
                          ) || "-",
                        ],
                        [
                          "Study Type",
                          selectedStudy.protocolSection.designModule
                            ?.studyType || "-",
                        ],
                        [
                          "Enrollment",
                          selectedStudy.protocolSection.designModule
                            ?.enrollmentInfo?.count || "-",
                        ],
                        [
                          "Status",
                          selectedStudy.protocolSection.statusModule
                            ?.overallStatus || "-",
                        ],
                      ].map(([label, value], i) => (
                        <div
                          key={i}
                          className="
                      rounded-lg
                      bg-white/5
                      border border-white/10
                      p-4
                    "
                        >
                          <p className="text-white/50">{label}</p>
                          <p className="font-medium text-white">{value}</p>
                        </div>
                      ))}
                    </div>

                    {/* DATES */}
                    <div className="grid grid-cols-3 gap-4">
                      {[
                        ["Study Start", startDate],
                        ["Study End", endDate],
                        ["Duration", duration],
                      ].map(([label, value], i) => (
                        <div
                          key={i}
                          className="
                      rounded-lg
                      bg-white/5
                      border border-white/10
                      p-4
                    "
                        >
                          <p className="text-white/50">{label}</p>
                          <p className="font-medium text-white">{value}</p>
                        </div>
                      ))}
                    </div>

                    {/* CONDITIONS */}
                    <div>
                      <h4 className="font-semibold text-white mb-2">
                        Conditions
                      </h4>
                      <div className="flex flex-wrap gap-2">
                        {selectedStudy?.protocolSection?.conditionsModule?.conditions?.map(
                          (c, i) => (
                            <span
                              key={i}
                              className="
                          px-3 py-1 text-xs
                          rounded-full
                          bg-cyan-400/10
                          text-cyan-300
                          border border-cyan-400/20
                          shadow-[0_0_12px_rgba(34,211,238,0.35)]
                        "
                            >
                              {c}
                            </span>
                          )
                        )}
                      </div>
                    </div>

                    {/* ELIGIBILITY */}
                    <div>
                      <h4 className="font-semibold text-white mb-2">
                        Eligibility Criteria
                      </h4>
                      <div
                        className="
                    bg-white/5
                    border border-white/10
                    rounded-lg
                    p-4
                    text-xs
                    text-white/70
                    whitespace-pre-wrap
                  "
                      >
                        {selectedStudy?.protocolSection?.eligibilityModule
                          ?.eligibilityCriteria || "Not specified"}
                      </div>
                    </div>

                    {/* STUDY DESIGN */}
                    <div>
                      <h4 className="font-semibold text-white mb-2">
                        Study Design
                      </h4>
                      <ul className="space-y-1 text-white/70">
                        <li>
                          <strong className="text-white">Allocation:</strong>{" "}
                          {selectedStudy?.protocolSection?.designModule
                            ?.designInfo?.allocation || "-"}
                        </li>
                        <li>
                          <strong className="text-white">
                            Intervention Model:
                          </strong>{" "}
                          {selectedStudy?.protocolSection?.designModule
                            ?.designInfo?.interventionModel || "-"}
                        </li>
                        <li>
                          <strong className="text-white">Masking:</strong>{" "}
                          {selectedStudy?.protocolSection?.designModule
                            ?.designInfo?.maskingInfo?.masking || "-"}
                        </li>
                      </ul>
                    </div>

                    {/* SPONSOR */}
                    <div>
                      <h4 className="font-semibold text-white mb-2">
                        Lead Sponsor
                      </h4>
                      <p className="text-white/70">
                        {selectedStudy.protocolSection
                          .sponsorCollaboratorsModule?.leadSponsor?.name || "-"}
                      </p>
                    </div>

                    {/* RESPONSIBLE PARTY */}
                    <div className="grid grid-cols-2 gap-4">
                      {[
                        [
                          "Type",
                          selectedStudy.protocolSection
                            .sponsorCollaboratorsModule?.responsibleParty?.type,
                        ],
                        [
                          "Investigator Name",
                          selectedStudy.protocolSection
                            .sponsorCollaboratorsModule?.responsibleParty
                            ?.investigatorFullName,
                        ],
                        [
                          "Investigator Title",
                          selectedStudy.protocolSection
                            .sponsorCollaboratorsModule?.responsibleParty
                            ?.investigatorTitle,
                        ],
                        [
                          "Affiliation",
                          selectedStudy.protocolSection
                            .sponsorCollaboratorsModule?.responsibleParty
                            ?.investigatorAffiliation,
                        ],
                      ].map(([label, value], i) => (
                        <div
                          key={i}
                          className="
                      rounded-lg
                      bg-white/5
                      border border-white/10
                      p-4
                    "
                        >
                          <p className="text-white/50">{label}</p>
                          <p className="font-medium text-white">
                            {value || "-"}
                          </p>
                        </div>
                      ))}
                    </div>
                  </>
                );
              })()
            ) : null}
          </div>
        </div>
      )}
    </div>
  );
};

export default Studies_Calculator;
