import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import {
  ChevronRight,
  Home,
  LayoutDashboard,
  Bot,
  BookOpen,
  Waypoints,
  Users,
  ListTree,
  Megaphone,
  FileAudio,
  HelpCircle,
  Settings
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { motion } from 'framer-motion';

// Map of route segments to their display labels and icons
const routeMap: Record<string, { label: string; icon?: React.ReactNode }> = {
  '': { label: 'Home', icon: <Home className="h-3.5 w-3.5" /> },
  'dashboard': { label: 'Dashboard', icon: <LayoutDashboard className="h-3.5 w-3.5" /> },
  'agents': { label: 'Agents', icon: <Bot className="h-3.5 w-3.5" /> },
  'knowledge-bases': { label: 'Knowledge Bases', icon: <BookOpen className="h-3.5 w-3.5" /> },
  'actions': { label: 'Actions', icon: <Waypoints className="h-3.5 w-3.5" /> },
  'contacts': { label: 'Contacts', icon: <Users className="h-3.5 w-3.5" /> },
  'lists': { label: 'Lists', icon: <ListTree className="h-3.5 w-3.5" /> },
  'campaigns': { label: 'Campaigns', icon: <Megaphone className="h-3.5 w-3.5" /> },
  'recordings': { label: 'Recordings', icon: <FileAudio className="h-3.5 w-3.5" /> },
  'support': { label: 'Support', icon: <HelpCircle className="h-3.5 w-3.5" /> },
  'settings': { label: 'Settings', icon: <Settings className="h-3.5 w-3.5" /> },
  'leads-contacts': { label: 'Leads', icon: <Users className="h-3.5 w-3.5" /> },
  'leads-scraper': { label: 'Leads Scraper', icon: <Users className="h-3.5 w-3.5" /> },
  'marketing-ads': { label: 'Marketing Ads', icon: <Megaphone className="h-3.5 w-3.5" /> },
  'phone-numbers': { label: 'Phone Numbers' },
  'outbound-calls': { label: 'Outbound Calls' },
  'outbound': { label: 'Outbound' },
  'workflows': { label: 'Workflows' },
  'webhooks': { label: 'Webhooks' },
  'credits': { label: 'Credits' },
  'audit-logs': { label: 'Audit Logs' },
  'users': { label: 'Users', icon: <Users className="h-3.5 w-3.5" /> },
  'roles': { label: 'Roles' },
  'permissions': { label: 'Permissions' },
  'super-admin': { label: 'Super Admin' },
  'integration': { label: 'Integrations' },
  'models': { label: 'Models' },
  'voices': { label: 'Voices' },
  'messages': { label: 'Messages' },
  'patients': { label: 'Patients' },
  'studies_calculator': { label: 'Studies Calculator' },
  'email_management': { label: 'Email Management' },
  'default-assistants': { label: 'Default Assistants' },
  'leads': { label: 'Lead Details' },
};

// Map of path patterns to their parent routes (for nested routes that don't have their own page)
// Format: 'current-path-pattern' -> 'redirect-to-path'
const pathRedirectMap: Record<string, string> = {
  '/studies': '/studies_calculator',
  '/studies/db': '/studies_calculator',
};

interface BreadcrumbsProps {
  className?: string;
}

const Breadcrumbs: React.FC<BreadcrumbsProps> = ({ className }) => {
  const location = useLocation();

  // Split the pathname into segments and filter out empty segments
  const pathSegments = location.pathname.split('/').filter(segment => segment);

  // If we're at the root, just show Home
  if (pathSegments.length === 0) {
    return (
      <nav className={cn("flex items-center space-x-1 text-sm", className)} aria-label="Breadcrumbs">
        <span className="flex items-center text-primary font-medium">
          <Home className="h-3.5 w-3.5 mr-1" />
          <span>Home</span>
        </span>
      </nav>
    );
  }

  // Build breadcrumb items, filtering out intermediate paths that don't have routes
  const breadcrumbItems: Array<{ path: string; label: string; icon: React.ReactNode | null; isLast: boolean }> = [];

  pathSegments.forEach((segment, index) => {
    // Build the actual path up to this segment
    const path = '/' + pathSegments.slice(0, index + 1).join('/');

    // Determine if this is the last item (current page)
    const isLast = index === pathSegments.length - 1;

    // Check if this path needs to be redirected (intermediate path without its own route)
    const redirectPath = pathRedirectMap[path];

    // If this is an intermediate path that redirects, skip it to avoid showing confusing breadcrumbs
    // But always show the last item (current page)
    if (redirectPath && !isLast) {
      return; // Skip this intermediate breadcrumb
    }

    // Get display info from our map, or format the segment as label
    const routeInfo = routeMap[segment];
    const label = routeInfo?.label || segment.split('-').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ');
    const icon = routeInfo?.icon || null;

    breadcrumbItems.push({
      path: redirectPath || path, // Use redirect path if available, otherwise actual path
      label,
      icon,
      isLast
    });
  });

  // Add Home as the first item
  breadcrumbItems.unshift({
    path: '/',
    label: 'Home',
    icon: <Home className="h-3.5 w-3.5" />,
    isLast: false
  });

  return (
    <nav
      className={cn(
        "flex items-center text-sm overflow-x-auto scrollbar-hide",
        className
      )}
      aria-label="Breadcrumbs"
    >
      <div className="flex items-center space-x-1.5">
        {breadcrumbItems.map((item, index) => (
          <React.Fragment key={item.path}>
            {index > 0 && (
              <ChevronRight className="h-3.5 w-3.5 text-muted-foreground flex-shrink-0 opacity-70" />
            )}

            {item.isLast ? (
              <span className="flex items-center text-foreground font-medium px-2 py-1 rounded-md bg-secondary/40">
                {item.icon && (
                  <span className="mr-1.5 text-primary">{item.icon}</span>
                )}
                <span className="truncate max-w-[150px]">{item.label}</span>
              </span>
            ) : (
              <motion.div
                whileHover={{ scale: 1.02 }}
                transition={{ duration: 0.2 }}
                className="group"
              >
                <Link
                  to={item.path}
                  className="flex items-center text-muted-foreground hover:text-primary transition-colors px-2 py-1 rounded-md hover:bg-secondary/50"
                >
                  {item.icon && (
                    <span className="mr-1.5 opacity-70 group-hover:opacity-100 text-muted-foreground group-hover:text-primary transition-colors">{item.icon}</span>
                  )}
                  <span className="truncate max-w-[120px]">{item.label}</span>
                </Link>
              </motion.div>
            )}
          </React.Fragment>
        ))}
      </div>
    </nav>
  );
};

export default Breadcrumbs;
