import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { LogOut, X, AlertTriangle, Loader2 } from "lucide-react";
import { Button } from "@/components/ui/button";

interface LogoutModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: () => Promise<void> | void;
}

const LogoutModal: React.FC<LogoutModalProps> = ({
  isOpen,
  onClose,
  onConfirm,
}) => {
  const [isLoggingOut, setIsLoggingOut] = useState(false);

  const handleConfirm = async () => {
    if (isLoggingOut) return;
    try {
      setIsLoggingOut(true);
      await onConfirm();
    } finally {
      // in case parent does not immediately unmount modal
      setIsLoggingOut(false);
    }
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          {/* BACKDROP */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => !isLoggingOut && onClose()}
            className="fixed inset-0 z-50 bg-black/70 backdrop-blur-md"
          />

          {/* MODAL */}
          <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
            <motion.div
              initial={{ opacity: 0, scale: 0.85, y: 60 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.85, y: -40 }}
              transition={{ type: "spring", stiffness: 280, damping: 26 }}
              className="relative w-full max-w-2xl"
            >
              {/* NEON GLOW */}
              <div
                className="absolute -inset-1 rounded-2xl
                bg-gradient-to-r from-yellow-400/30 via-orange-500/30 to-red-600/30
                blur-xl opacity-80"
              />

              {/* CARD */}
              <div
                className="relative rounded-2xl overflow-hidden
                bg-gradient-to-br from-[#0b0f19] via-[#0f172a] to-[#020617]
                border border-yellow-400/30
                shadow-[0_0_10px_rgba(255,170,0,0.25)]"
              >
                {/* HEADER */}
                <div className="relative p-8">
                  <button
                    onClick={() => !isLoggingOut && onClose()}
                    disabled={isLoggingOut}
                    className="absolute top-4 right-4 p-2 rounded-full
                      hover:bg-white/10 transition
                      disabled:opacity-40"
                  >
                    <X className="w-5 h-5 text-white" />
                  </button>

                  <div className="flex items-center gap-6">
                    <div
                      className="p-4 rounded-full
                        bg-gradient-to-br from-yellow-400 to-orange-500
                        shadow-[0_0_20px_rgba(255,170,0,0.9)]"
                    >
                      <AlertTriangle className="w-9 h-9 text-black" />
                    </div>

                    <div>
                      <h2 className="text-lg font-extrabold tracking-wide text-white">
                        Confirm Logout
                      </h2>
                      <p className="mt-2 text-white/70 text-md">
                        You are about to end your session
                      </p>
                    </div>
                  </div>
                </div>

                {/* CONTENT */}
                <div className="px-8 pb-8 space-y-8">
                  {/* WARNING BOX */}
                  <div
                    className="flex gap-4 p-6 rounded-xl
                      bg-black/40
                      border border-orange-500/30
                      shadow-inner"
                  >
                    <LogOut className="w-6 h-6 text-orange-400 mt-1" />
                    <div>
                      <p className="text-md font-semibold text-white">
                        You will be logged out
                      </p>
                      <p className="mt-1 text-white/60">
                        Unsaved progress may be lost. Please confirm.
                      </p>
                    </div>
                  </div>

                  {/* ACTIONS */}
                  <div className="flex gap-4">
                    <Button
                      variant="outline"
                      onClick={onClose}
                      disabled={isLoggingOut}
                      className="
                        flex-1 h-14
                        text-md font-medium
                        text-white
                        border-white/20
                        bg-white/10
                        hover:bg-white/20
                        disabled:opacity-40
                      "
                    >
                      Cancel
                    </Button>

                    {/* CONFIRM BUTTON WITH LOADING */}
                    <Button
                      onClick={handleConfirm}
                      disabled={isLoggingOut}
                      className="
                        flex-1 h-14
                        text-md font-bold
                        text-white
                        bg-gradient-to-r
                        from-yellow-400 via-orange-500 to-red-600
                        shadow-[0_0_10px_rgba(255,140,0,0.9)]
                        hover:shadow-[0_0_14px_rgba(255,90,0,1)]
                        transition-all
                        disabled:opacity-70
                      "
                    >
                      {isLoggingOut ? (
                        <span className="flex items-center gap-3">
                          <Loader2 className="w-5 h-5 animate-spin" />
                          Logging out
                        </span>
                      ) : (
                        <>
                          <LogOut className="w-6 h-6 mr-3" />
                          Yes, Logout
                        </>
                      )}
                    </Button>
                  </div>
                </div>

                {/* BOTTOM LINE */}
                <div className="h-[3px] bg-gradient-to-r from-yellow-400 via-orange-500 to-red-600" />
              </div>
            </motion.div>
          </div>
        </>
      )}
    </AnimatePresence>
  );
};

export default LogoutModal;
