import { memo, useEffect, useState } from "react";

interface FastLoadingProps {
  delay?: number;
  minDuration?: number;
}

const FastLoading = memo(({ delay = 200, minDuration = 200 }: FastLoadingProps) => {
  const [showLoading, setShowLoading] = useState(false);
  const [startTime, setStartTime] = useState<number | null>(null);

  useEffect(() => {
    const delayTimer = setTimeout(() => {
      setShowLoading(true);
      setStartTime(Date.now());
    }, delay);

    return () => clearTimeout(delayTimer);
  }, [delay]);

  useEffect(() => {
    if (showLoading && startTime) {
      const elapsed = Date.now() - startTime;
      if (elapsed < minDuration) {
        const timer = setTimeout(() => {}, minDuration - elapsed);
        return () => clearTimeout(timer);
      }
    }
  }, [showLoading, startTime, minDuration]);

  if (!showLoading) return null;

  return (
    <div className="fixed inset-0 z-[9999] flex items-center justify-center overflow-hidden">
      {/*  NEON BACKGROUND */}
      <div className="absolute inset-0 bg-gradient-to-br from-[#0f0c29] via-[#302b63] to-[#24243e]" />
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_top,rgba(255,140,0,0.25),transparent_60%)]" />
      <div className="absolute inset-0 bg-[radial-gradient(circle_at_bottom,rgba(0,180,255,0.25),transparent_60%)]" />
      <div className="absolute inset-0 backdrop-blur-md bg-black/30" />

      {/*  CONTENT */}
      <div className="relative flex flex-col items-center gap-10">

        {/*  FIRE LOADER */}
        <div className="relative">
          <div className="w-20 h-20 border-4 border-orange-400/30 rounded-full animate-spin" />

          <div
            className="absolute inset-2 w-16 h-16 border-4
              border-t-yellow-400
              border-r-orange-500
              border-b-red-500
              border-l-yellow-400
              rounded-full animate-spin"
            style={{ animationDirection: "reverse", animationDuration: "1.4s" }}
          />

          <div
            className="
              absolute inset-6 w-8 h-8 rounded-full
              bg-gradient-to-br from-yellow-300 via-orange-400 to-red-600
              animate-pulse
              shadow-[0_0_20px_rgba(255,170,0,0.95)]
            "
          />

          <div
            className="absolute inset-0 w-20 h-20 animate-spin"
            style={{ animationDuration: "3s" }}
          >
            <div className="absolute top-0 left-1/2 w-2 h-2 bg-yellow-400 rounded-full -translate-x-1/2" />
            <div className="absolute bottom-0 left-1/2 w-2 h-2 bg-orange-500 rounded-full -translate-x-1/2" />
          </div>

          <div
            className="
              absolute inset-0 w-20 h-20 rounded-full blur-xl
              bg-gradient-to-br from-yellow-400/30 via-orange-500/30 to-red-600/30
            "
          />
        </div>

        {/*  FIRE TEXT */}
        <div className="text-center">
          <h2
            className="
              text-4xl font-extrabold tracking-widest
              text-yellow-300
              animate-fireText
              drop-shadow-[0_0_2px_rgba(255,200,0,1)]
              drop-shadow-[0_0_4px_rgba(255,140,0,0.9)]
              drop-shadow-[0_0_5px_rgba(255,80,0,0.8)]
              mb-2
            "
          >
            AI CRUITMENT
          </h2>

          <div className="flex items-center justify-center gap-2 mt-3">
            <span className="text-white/90 text-lg">Loading</span>
            <div className="flex gap-1">
              <span className="w-1 h-1 bg-yellow-400 rounded-full animate-bounce" />
              <span className="w-1 h-1 bg-orange-500 rounded-full animate-bounce delay-150" />
              <span className="w-1 h-1 bg-red-500 rounded-full animate-bounce delay-300" />
            </div>
          </div>
        </div>

        {/*  PROGRESS BAR */}
        <div className="w-64 h-1 rounded-full overflow-hidden bg-white/10">
          <div
            className="h-full bg-gradient-to-r from-yellow-400 via-orange-500 to-red-600 rounded-full"
            style={{ animation: "fireProgress 2s ease-in-out infinite" }}
          />
        </div>
      </div>

      {/*  FLOATING NEON PARTICLES */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-2 h-2 bg-yellow-400/40 rounded-full animate-ping" />
        <div className="absolute top-1/3 right-1/4 w-1 h-1 bg-orange-500/40 rounded-full animate-ping delay-1000" />
        <div className="absolute bottom-1/3 left-1/3 w-1.5 h-1.5 bg-cyan-400/40 rounded-full animate-ping delay-2000" />
      </div>

      {/*  KEYFRAMES */}
      <style
        dangerouslySetInnerHTML={{
          __html: `
            @keyframes fireText {
              0% {
                color: #ffd966;
                text-shadow:
                  0 0 8px rgba(255,200,0,1),
                  0 0 18px rgba(255,140,0,0.9),
                  0 0 30px rgba(255,80,0,0.8);
              }
              50% {
                color: #ffb703;
                text-shadow:
                  0 0 14px rgba(255,180,0,1),
                  0 0 30px rgba(255,120,0,1),
                  0 0 46px rgba(255,60,0,0.9);
              }
              100% {
                color: #ffd966;
                text-shadow:
                  0 0 8px rgba(255,200,0,1),
                  0 0 18px rgba(255,140,0,0.9),
                  0 0 30px rgba(255,80,0,0.8);
              }
            }

            .animate-fireText {
              animation: fireText 1.8s ease-in-out infinite;
            }

            @keyframes fireProgress {
              0% { transform: translateX(-100%); }
              50% { transform: translateX(300%); }
              100% { transform: translateX(-100%); }
            }
          `,
        }}
      />
    </div>
  );
});

FastLoading.displayName = "FastLoading";

export default FastLoading;







