const VoiceWaveform: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const resizeCanvas = () => {
      const parent = canvas.parentElement;
      if (parent) {
        canvas.width = parent.clientWidth;
        canvas.height = 120;
      }
    };

    resizeCanvas();
    window.addEventListener("resize", resizeCanvas);

    let animationFrameId: number;
    let time = 0;

    const drawWaveform = () => {
      if (!ctx || !canvas) return;

      ctx.clearRect(0, 0, canvas.width, canvas.height);

      const centerY = canvas.height / 2;
      const amplitude = 30;
      const frequency = 0.02;
      const speed = 0.05;

      /* =======================
          PRIMARY FIRE WAVE
      ======================= */
      ctx.beginPath();
      ctx.moveTo(0, centerY);

      for (let x = 0; x < canvas.width; x++) {
        const y1 = Math.sin(x * frequency + time) * amplitude * 0.6;
        const y2 = Math.sin(x * frequency * 1.4 + time * 1.2) * amplitude * 0.3;
        const y3 = Math.sin(x * frequency * 0.6 + time) * amplitude * 0.2;
        ctx.lineTo(x, centerY + y1 + y2 + y3);
      }

      const fireGradient = ctx.createLinearGradient(0, 0, canvas.width, 0);
      fireGradient.addColorStop(0, "rgba(255,180,0,0.2)");
      fireGradient.addColorStop(0.5, "rgba(255,120,0,0.9)");
      fireGradient.addColorStop(1, "rgba(255,60,0,0.2)");

      ctx.strokeStyle = fireGradient;
      ctx.lineWidth = 2.5;
      ctx.shadowBlur = 20;
      ctx.shadowColor = "rgba(255,140,0,0.9)";
      ctx.stroke();

      /* =======================
          SECONDARY GLOW WAVE
      ======================= */
      ctx.beginPath();
      ctx.moveTo(0, centerY);

      for (let x = 0; x < canvas.width; x++) {
        const y1 = Math.sin(x * frequency * 0.8 + time * 1.4) * amplitude * 0.4;
        const y2 = Math.sin(x * frequency * 1.1 + time) * amplitude * 0.2;
        ctx.lineTo(x, centerY + y1 + y2);
      }

      const glowGradient = ctx.createLinearGradient(0, 0, canvas.width, 0);
      glowGradient.addColorStop(0, "rgba(255,255,255,0.05)");
      glowGradient.addColorStop(0.5, "rgba(255,200,120,0.4)");
      glowGradient.addColorStop(1, "rgba(255,255,255,0.05)");

      ctx.strokeStyle = glowGradient;
      ctx.lineWidth = 1.5;
      ctx.shadowBlur = 10;
      ctx.shadowColor = "rgba(255,200,120,0.6)";
      ctx.stroke();

      /* =======================
          FIRE PARTICLES
      ======================= */
      const particleCount = 18;
      for (let i = 0; i < particleCount; i++) {
        const x =
          (canvas.width / particleCount) * i + Math.sin(time * 2 + i) * 6;
        const yOffset = Math.sin(time * 3 + i) * amplitude * 0.6;
        const y = centerY + yOffset;

        ctx.beginPath();
        ctx.arc(x, y, Math.abs(yOffset) / 10 + 1.2, 0, Math.PI * 2);
        ctx.fillStyle = "rgba(255,180,80,0.8)";
        ctx.shadowBlur = 12;
        ctx.shadowColor = "rgba(255,140,0,1)";
        ctx.fill();
      }

      time += speed;
      animationFrameId = requestAnimationFrame(drawWaveform);
    };

    drawWaveform();

    return () => {
      window.removeEventListener("resize", resizeCanvas);
      cancelAnimationFrame(animationFrameId);
    };
  }, []);

  return (
    <canvas
      ref={canvasRef}
      className="
        w-full h-[120px]
        opacity-80
        rounded-xl
        backdrop-blur-md
        
      "
    />
  );
};

