import React, { useState } from "react";
import axiosInstance from "@/api/axios/axiosInstance";

const TOTAL_STEPS = 6;

const StudyAnalysisStepper = ({ study }) => {
  const [step, setStep] = useState(0);
  const [analysis, setAnalysis] = useState({});
  const [loading, setLoading] = useState(false);

  if (!study) return null;

  const delay = (ms) => new Promise((res) => setTimeout(res, ms));

  const buildFinalPayload = (protocol, difficulty, cost, confidence) => ({
    nctId: study.protocolSection.identificationModule.nctId,

    phase: protocol.phase,
    studyType: protocol.studyType,
    conditionCount: protocol.conditionCount,
    eligibilityLength: protocol.eligibilityLength,
    durationMonths: protocol.durationMonths,

    complexityLabel: protocol.complexityLabel,
    difficultyLabel: difficulty.label,

    enrollment: protocol.enrollment,
    cpe: cost.cpe,
    estimatedTotalCost: cost.estimatedTotalCost,

    confidence: confidence.confidence,

    geo: "US",
    siteCount: 10,
  });

  // const startCalculation = async () => {
  //   try {
  //     setLoading(true);
  //     setAnalysis({});
  //     setStep(1);

  //     const protocol = await axiosInstance.post(
  //       "/api/studies/analyze-protocol",
  //       { study }
  //     );
  //     setAnalysis(protocol.data);
  //     await delay(600);

  //     setStep(2);
  //     const difficulty = await axiosInstance.post(
  //       "/api/studies/difficulty",
  //       protocol.data
  //     );
  //     setAnalysis(p => ({ ...p, ...difficulty.data }));
  //     await delay(600);

  //     setStep(3);
  //     const cost = await axiosInstance.post("/api/studies/cost", {
  //       difficultyLabel: difficulty.data.label,
  //       enrollment: protocol.data.enrollment
  //     });
  //     setAnalysis(p => ({ ...p, ...cost.data }));
  //     await delay(600);

  //     setStep(4);
  //     const confidence = await axiosInstance.post(
  //       "/api/studies/confidence",
  //       protocol.data
  //     );
  //     setAnalysis(p => ({ ...p, ...confidence.data }));
  //     await delay(600);

  //     setStep(5);

  //     setStep(6);
  //     const finalPayload = buildFinalPayload(
  //       protocol.data,
  //       difficulty.data,
  //       cost.data,
  //       confidence.data
  //     );

  //   } catch (e) {
  //     console.error(e);
  //     setStep(0);
  //   } finally {
  //     setLoading(false);
  //   }
  // };

  const startCalculation = async () => {
    try {
      setLoading(true);
      setAnalysis({});
      setStep(1);

      const protocol = await axiosInstance.post(
        "/api/studies/analyze-protocol",
        { study }
      );
      setAnalysis(protocol.data);
      await delay(500);

      setStep(2);
      const difficulty = await axiosInstance.post(
        "/api/studies/difficulty",
        protocol.data
      );
      setAnalysis((p) => ({ ...p, ...difficulty.data }));
      await delay(500);

      setStep(3);
      const cost = await axiosInstance.post("/api/studies/cost", {
        difficultyLabel: difficulty.data.label,
        enrollment: protocol.data.enrollment,
      });
      setAnalysis((p) => ({ ...p, ...cost.data }));
      await delay(500);

      setStep(4);
      const confidence = await axiosInstance.post(
        "/api/studies/confidence",
        protocol.data
      );
      setAnalysis((p) => ({ ...p, ...confidence.data }));
      await delay(500);

      const finalPayload = buildFinalPayload(
        protocol.data,
        difficulty.data,
        cost.data,
        confidence.data
      );

      setAnalysis((p) => ({ ...p, finalPayload }));
      setStep(6);
    } catch (e) {
      console.error(e);
      setStep(0);
    } finally {
      setLoading(false);
    }
  };

  const downloadProposal = async () => {
    const res = await axiosInstance.post(
      "/api/studies/proposal/pdf",
      analysis.finalPayload,
      { responseType: "blob" }
    );

    const blob = new Blob([res.data], { type: "application/pdf" });
    const url = window.URL.createObjectURL(blob);

    const a = document.createElement("a");
    a.href = url;
    a.download = `proposal-${analysis.finalPayload.nctId}.pdf`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    window.URL.revokeObjectURL(url);
  };

  const MetricRow = ({ label, value, color }) => {
    const colors = {
      cyan: "from-cyan-400/30 to-cyan-600/10 shadow-[0_0_18px_rgba(34,211,238,0.35)]",
      blue: "from-blue-400/30 to-blue-600/10 shadow-[0_0_18px_rgba(59,130,246,0.35)]",
      emerald:
        "from-emerald-400/30 to-emerald-600/10 shadow-[0_0_18px_rgba(16,185,129,0.35)]",
      pink: "from-pink-400/30 to-pink-600/10 shadow-[0_0_18px_rgba(250,24,96,0.35)]",
    };
  
    return (
      <div
        className={`
          flex items-center justify-between
          px-4 py-2.5
          rounded-lg
          bg-gradient-to-r ${colors[color]}
          border border-white/10
          backdrop-blur-md
        `}
      >
        <span className="text-sm tracking-wide text-white/60">
          {label}
        </span>
  
        <span className="text-sm font-semibold text-white">
          {value}
        </span>
      </div>
    );
  };
  

  return (
    <div
      className="
    p-6 rounded-2xl space-y-6
    bg-[linear-gradient(180deg,#0b0f2e_0%,#090c26_100%)]
    border border-white/10
    shadow-[0_30px_80px_rgba(0,0,0,0.7)]
  "
    >
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-semibold text-white">
          Predictive Enrollment Engineering
        </h3>

        <button
          onClick={startCalculation}
          disabled={loading}
          className="
      px-4 py-2 rounded-lg font-semibold
      text-white
      bg-[linear-gradient(180deg,rgba(90,110,255,0.45),rgba(36,46,151,0.35))]
      shadow-[0_0_8px_rgba(34,211,238,0.7)]
      disabled:opacity-50
      transition
    "
        >
          {loading ? "Processing" : "Calculate"}
        </button>
      </div>

      <div className="h-2 rounded bg-black/30 overflow-hidden">
        <div
          className="
      h-full
      bg-gradient-to-r from-cyan-400 to-blue-500
      shadow-[0_0_15px_rgba(34,211,238,0.8)]
      transition-all
    "
          style={{ width: `${(step / TOTAL_STEPS) * 100}%` }}
        />
      </div>

      {step >= 5 && (
        <div
          className="
        p-5 rounded-xl space-y-3
       bg-[linear-gradient(180deg,#0b0f2e_0%,#090c26_100%)]
        
        text-white
        border border-cyan-400/20
      "
        >
         <MetricRow
      label="Confidence"
      value={`${analysis.confidence}%`}
      color="cyan"
    />
    <MetricRow
      label="Difficulty"
      value={analysis.label}
      color="blue"
    />
    <MetricRow
      label="CPE"
      value={`$${analysis.cpe}`}
      color="emerald"
    />
    <MetricRow
      label="Total Cost"
      value={`$${analysis.estimatedTotalCost?.toLocaleString()}`}
      color="pink"
    />
        </div>
      )}

      {/* {step === 6 && (
        <button
          onClick={downloadProposal}
          className="
    px-5 py-2 rounded-lg font-semibold
    text-white
    bg-gradient-to-r from-emerald-400 to-green-500
    shadow-[0_0_25px_rgba(16,185,129,0.7)]
    hover:shadow-[0_0_35px_rgba(16,185,129,1)]
    transition
  "
        >
          Download Proposal PDF
        </button>
      )} */}
    </div>
  );
};

export default StudyAnalysisStepper;
